import React from 'react'
import {useQuery} from 'react-query'
import axios from 'axios'
import { LineChart, Line, XAxis, YAxis,Tooltip, CartesianGrid} from 'recharts';
import Accordion from 'react-bootstrap/Accordion';


async function fetchData(){
    const { data } = await axios.get('/data')
    return data
}

function Table(){
    
    const {data, error, isError, isLoading } = useQuery('posts', fetchData) 

    if(isLoading){
        return <div className="container"><div className="row">Loading...</div></div>
    }
    if(isError){
        return <div className="container"><div className="row">Error! {error.message}</div></div>
    }

    const prefixes = data['Prefixes']
    const devs =  data['Data']
    
    return(
        <div className='container'>

            <div className='row'>
                <div className="col-md-7 pr-3">
                    <h3>Devices Found</h3>
                    <p>Here are the IPv6 enabled devices on your network. <br/>Highlighted entries are devices with EUI-64 based addresses.</p>
                    
                </div>
                <div className="col-md-5 mt-1">
                    <PrefixInfo data={prefixes}></PrefixInfo>
                </div>
                
                <div className='col-md-12 mt-3'>
                    <table className="table">
                        <thead>
                            <tr>
                            <th scope="col">#</th>
                            <th scope="col">MAC Address</th>
                            <th scope="col">Vendor</th>
                            <th scope="col">Name</th>
                            <th scope="col">Link-Local Address</th>
                            <th scope="col">Global Address</th>
                            <th scope="col">EUI64</th>
                            </tr>
                        </thead>
                        <tbody>
                            {Object.keys(devs).map((item, i) => (
                                <tr key={i} className={"" + (devs[item]['isEUI64'] === "True" ? 'bg-warning' : 'bg-white')}>
                                    <th scope="row">{i}</th>
                                    <td scope="col">{item}</td>
                                    <td scope="col">{devs[item]['Vendor']}</td>
                                    <td scope="col">{devs[item]['device-name']}</td>
                                    <td scope="col">{devs[item]['Local']}</td>
                                    <td scope="col">{devs[item]['Global']}</td>
                                    <td scope="col">{devs[item]['isEUI64']}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
                
            </div>
        </div>
    )
}


function PrefixInfo(props) {
    const formatXAxis = (tickFormat) => {
            return new Date(tickFormat).toLocaleDateString("en-US")
    };

    const formatXXAxis = (tickFormat) => {
            let dd = new Date(tickFormat)
            return dd.toLocaleDateString("en-US") + ' '+ dd.toLocaleTimeString()
    };

    function countUnique(iterable) {
      return new Set(iterable).size;
    }

    const prefixs_unique = countUnique(props.data.map(item => item.Prefix))

    return (
    <Accordion defaultActiveKey="1">
        <Accordion.Item eventKey="0">
        <Accordion.Header>Prefix Information</Accordion.Header>
        <Accordion.Body>
            <p>Unique Prefixes Observed: {prefixs_unique}</p>
            <LineChart
                width={500} height={200} data={props.data}
                margin={{ top: 20, right: 40, bottom: 20, left: -10 }}
            >
            <CartesianGrid vertical={false} />
            <XAxis dataKey="Timestamp" label="Time" tickFormatter={(tick) => formatXAxis(tick)} tick={{fontSize: 8}}/>
            <YAxis type='category' tick={{fontSize: 0}} label="Prefix"></YAxis>
            <Tooltip
            wrapperStyle={{
                borderColor: 'white',
                boxShadow: '2px 2px 3px 0px rgb(204, 204, 204)',
            }}
            contentStyle={{ backgroundColor: 'rgba(255, 255, 255, 0.8)' }}
            labelStyle={{ fontWeight: 'bold', color: '#666666' }}
            // tickFormatter={(tick, tock) => console.log(tick,tock)}
            labelFormatter={(label) => formatXXAxis(label)}
            
            />
            <Line dataKey="Prefix" type='category' stroke="#0c63e4" dot={true} />
                
            </LineChart>
        </Accordion.Body>
        </Accordion.Item>
        
    </Accordion>
  );
}

export default Table